import axios from 'axios';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL || 'http://localhost:8000/api';

const apiService = axios.create({
  baseURL: API_BASE_URL,
});

const handleResponse = (response) => response.data;
const handleError = (error) => {
  console.error('API call error:', error);
  throw error;
};

const getAssignmentId = () => {
  return window.ASSIGNMENT_DATA ? window.ASSIGNMENT_DATA.id : null;
};

const apiEndpoints = {
  scormAssignment: {
    get: () => apiService.get(`/scorm/assignments/${getAssignmentId()}/`).then(handleResponse).catch(handleError),
    submit: (data) => apiService.post(`/scorm/assignments/${getAssignmentId()}/submit/`, data).then(handleResponse).catch(handleError),
    getScormData: () => apiService.get(`/scorm/assignments/${getAssignmentId()}/scorm_data/`).then(handleResponse).catch(handleError),
    updateScormData: (data) => apiService.post(`/scorm/assignments/${getAssignmentId()}/scorm_data/`, data).then(handleResponse).catch(handleError),
  },
};

export default apiEndpoints;