import React, { useState, useEffect } from 'react';
import apiEndpoints from '../apiService';

const SubmissionForm = ({ onBack }) => {
  const [assignment, setAssignment] = useState(null);
  const [submissionText, setSubmissionText] = useState('');
  const [studentName, setStudentName] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [submitting, setSubmitting] = useState(false);
  const [result, setResult] = useState(null);

  useEffect(() => {
    const fetchAssignment = async () => {
      try {
        const data = await apiEndpoints.scormAssignment.get();
        setAssignment(data);
        setLoading(false);
      } catch (err) {
        setError('Failed to fetch assignment details. Please try again later.');
        setLoading(false);
      }
    };

    fetchAssignment();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSubmitting(true);
    setError(null);

    try {
      const response = await apiEndpoints.scormAssignment.submit({
        text: submissionText,
        student_name: studentName
      });
      setResult(response);

      // Update SCORM data
      await window.ScormAPI.setValue('cmi.core.lesson_status', 'completed');
      await window.ScormAPI.setValue('cmi.core.score.raw', response.score);
      await window.ScormAPI.commit();

      setSubmitting(false);
    } catch (err) {
      setError('Failed to submit assignment. Please try again.');
      setSubmitting(false);
    }
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>Error: {error}</div>;
  }

  if (result) {
    return (
      <div className="max-w-4xl mx-auto mt-8 px-4">
        <h2 className="text-2xl font-bold mb-4">Submission Result</h2>
        <p>Score: {result.score}</p>
        <p>Feedback: {result.feedback}</p>
        <button
          onClick={onBack}
          className="mt-4 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
        >
          Back to Assignment
        </button>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto mt-8 px-4">
      <h1 className="text-3xl font-bold mb-6">Submit Assignment: {assignment?.title}</h1>
      <form onSubmit={handleSubmit} className="space-y-8 divide-y divide-gray-200">
        <div>
          <label htmlFor="student-name" className="block text-sm font-medium text-gray-700">
            Your Name
          </label>
          <input
            type="text"
            id="student-name"
            value={studentName}
            onChange={(e) => setStudentName(e.target.value)}
            className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            required
          />
        </div>
        <div>
          <label htmlFor="submission-text" className="block text-sm font-medium text-gray-700">
            Submission Text
          </label>
          <textarea
            id="submission-text"
            rows={10}
            value={submissionText}
            onChange={(e) => setSubmissionText(e.target.value)}
            className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            required
          />
        </div>
        <div className="pt-5">
          <div className="flex justify-end">
            <button
              type="button"
              onClick={onBack}
              className="bg-white py-2 px-4 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
            >
              Back
            </button>
            <button
              type="submit"
              disabled={submitting}
              className={`ml-3 inline-flex justify-center py-2 px-4 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 ${
                submitting ? 'opacity-50 cursor-not-allowed' : ''
              }`}
            >
              {submitting ? 'Submitting...' : 'Submit Assignment'}
            </button>
          </div>
        </div>
      </form>
    </div>
  );
};

export default SubmissionForm;